export const concatPackage = (packageArr = []) => {
    let connateArr = [];

    for (const packageItem of packageArr) {
        connateArr.push({
            id: packageItem.id,
            name: `${packageItem.name} (${Intl.NumberFormat().format(packageItem.price)} )`,
        })
    }

    return connateArr
}